import * as React from 'react'
import Layout from '../../components/layout'
import { MDXRenderer } from 'gatsby-plugin-mdx'
import { MDXProvider } from '@mdx-js/react';
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import {graphql} from 'gatsby'
import commentBox from 'commentbox.io'
import BlankLink from '../../components/blankLink'

const ArticlePostPage = ({data})=>{

  const refRemoveCommentBoxFn = React.useRef(null);

  React.useEffect(()=>{
    refRemoveCommentBoxFn.current = commentBox('5679622951796736-proj');
    return ()=>{
      refRemoveCommentBoxFn.current();
    };
  },[]);

  const img = getImage(data.mdx.frontmatter.heroImg)
  return (
    <Layout pageTitle={data.mdx.frontmatter.title} containerMaxWidth="1500">
      <div 
        className="border rounded"
        style={{backgroundColor:'white', padding:20}}>
          {/* Short desc */}
          <h5 className="mt-2">{data.mdx.frontmatter.shortDesc}</h5>

          {/* Date/Time */}
          <p className="small text-secondary text-end">貼於 {data.mdx.frontmatter.date} </p>

          <hr/>

          {/* Preview Image */}
          <div style={{display:'flex',flexDirection:'column', alignItems:'center'}}>
            <GatsbyImage className="img-fluid rounded" 
                          image={getImage(data.mdx.frontmatter.heroImg)} alt={data.mdx.frontmatter.heroImgAlt} 
                          style={{textAlign:'center'}}/>
          </div>
          <hr/>

          {/* Post Content */}
          <div style={{lineHeight:2, fontSize:'1.5rem'}}>
            <MDXProvider components={{a: BlankLink}}>
                <MDXRenderer 
                  localImages={data.mdx.frontmatter.embeddedImagesLocal} 
                  remoteImages={data.mdx.embeddedImagesRemote}
                  >
                  {data.mdx.body}
                </MDXRenderer>
              </MDXProvider>
          </div>
          <hr/>
      </div>
      
      <div  className="border rounded mt-4"
        style={{backgroundColor:'white', padding:20}}>
          {/* 留言 */}
          <h2>留 言</h2>
          <div className="commentbox" />
      </div>
    </Layout>
  )
}

export const query = graphql`
    query ($id: String) {
      mdx(id: {eq: $id}) {
        body

        embeddedImagesRemote {
          childImageSharp {
            gatsbyImageData
          } 
        }

        frontmatter {
          title
          date(formatString: "YYYY/MM/DD")
          shortDesc
          category
          tags
          heroImg{
            childImageSharp {
              gatsbyImageData
            }
          }
          heroImgAlt
          embeddedImagesLocal {
            childImageSharp {
              gatsbyImageData
            } 
          }
        }
      }
    }
`
export default ArticlePostPage